import React from 'react';
import Button from './Button.js'
import CardE from './CardE.js'
import css from './Controls.module.scss'
class Controls extends React.Component
{
    constructor(props)
    {
        super(props);
        this.colors=["primary","secondary", "success","warning", "danger","info","dark","light"];
        this.state={
            variant:1,
            clicked:props.clicked
        }
        this.ws=props.ws
    }

    render()
    {
        return(
         <div className={css.buttons}>
        <Button variant={this.colors[0]} text="Roll the dice" ws={this.ws} command="roll"/>
        <Button variant={this.colors[4]} text="End turn" ws={this.ws} command="end"/>
        <div className={css.card}>
        <br/>
        <CardE clicked={this.props.clicked}></CardE>
        </div>
        </div>
        )
    }
}

export default Controls;