import css from'./Player.module.scss';
import React from 'react';
import {TweenLite} from 'gsap';
class Player extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            cx2: props.cx,
            cy2: props.cy,
            level:props.level,
            r: props.r,
            color: props.color,
            pos:props.pos,
            max:props.max,
            y:0,
            angle:0,
            loaded:0,
            active:0,
            style:{},
            newstyle:{},
            empty:{}
            
        }
        this.myElement = null;
        this.myTween = null;
        this.setProps=this.setProps.bind(this)
        //this.getDerivedStateFromProps=getDerivedStateFromProps.bind(this);
    }
    componentDidMount()
    {
        this.setState({y:this.state.cy2 - 275 + 2.3 * this.state.r * this.state.level,angle:this.state.pos/this.state.max*360},()=>
        {
        var rr=this.state.cy2-this.state.y;
        var nx=Math.sin(this.state.angle*Math.PI/180)*rr+this.state.cx2;
        var ny=-Math.cos(this.state.angle*Math.PI/180)*rr+this.state.cy2;
        var style={
            fill:this.state.color,
            cx:nx,
            cy:ny,
            r:this.state.r
        }
        this.setState({newstyle:style,style:style,loaded:1})
    });
    }
    setProps()
    {
        var style={
            fill:this.state.color,
            cx:this.state.newstyle.cx,
            cy:this.state.newstyle.cy,
            r:this.state.r
        }
        this.setState({style})
    }
    componentDidUpdate()
    {
        if(this.state.loaded)
        {
        if(this.state.style.cx!==this.state.newstyle.cx||this.state.style.cy!==this.state.newstyle.cy)
        {
        this.myTween=TweenLite.fromTo(this.myElement, 1,{cx:this.state.style.cx,cy:this.state.style.cy} ,{cx: this.state.newstyle.cx, cy: this.state.newstyle.cy,onComplete: this.setProps})
        }
        }
    }
    static getDerivedStateFromProps(props, state) {
        if(state.loaded===1&&(state.pos!==props.pos||state.level!==props.level)&&props.r)
        {
            var y=state.cy2 - 275 + 2.3 * state.r*props.level;
            var rr=state.cy2-y;
            var angle=props.pos/state.max*360
            var nx=Math.sin(angle*Math.PI/180)*rr+state.cx2;
            var ny=-Math.cos(angle*Math.PI/180)*rr+state.cy2;
            var newstyle={
                cx:nx,
                cy:ny
            }
            /*
()=>{
            
            var angle=this.state.angle+12;
            var nx=Math.sin(angle*Math.PI/180)*rr+this.state.cx2;
            var ny=-Math.cos(angle*Math.PI/180)*rr+this.state.cy2;
            this.myTween=TweenLite.fromTo(this.myElement, 1,{cx:this.state.style.cx,cy:this.state.style.cy} ,{cx: nx, cy: ny})
        }
            */
        
        return {
            newstyle:newstyle,
            pos:props.pos,
            level:props.level
          };
        }
        return{}
    }
    render()
    {
        
        var classname=this.state.active?css.playerCircleActive : css.playerCircle;
        return(<circle ref={div=>this.myElement=div}style={this.state.style} className={classname}></circle>);
    }
}
export default Player;