import React from 'react';
import Property from './Property';
import Player from './Player';
import css from './Circle.module.scss'

class Circle extends React.Component
{
    constructor(props)
    {
        super(props);
        this.colors=["blue","red","pink","yellow"];
        this.test=0;
        this.state={
            background: 'gray',
            cx2: 1000,
            cy2: 410,
            test:0,
            circle: {},
            number: 30,
            pnum:4,
            pPos:[0,2,2,0],
            h:props.h/957*820,
            w:Math.min(820,props.w),
            data:props.data,
            clicked:-1,
            callback:props.callback
   
        }
        this.interval = this.interval.bind(this);
        this.intervalTime=1500;
        this.callback=this.callback.bind(this)
        
    }
    static getDerivedStateFromProps(props, state) {
        //console.log("RESIZED");
        return {
            h:props.h/957*820,
            w:Math.min(820,props.w),
          };
    }
    callback(num)
    {
        this.state.callback(num)
        this.setState({clicked:num})
    }
    drawCircle(r) {
        var el={};
        el.r=r;
        el.stroke="black";
        return el;
    }
    componentDidMount()
    {
        document.body.style.backgroundColor = this.state.background;
        var circle=this.drawCircle(300);
        this.setState({circle});
        var interval=setInterval(this.interval,this.intervalTime);
        this.setState({interval});
        //document.getElementById("canvas").innerHTML = document.getElementById("canvas").innerHTML;
        
  
    }
    

    interval()
    {
        /*
        var p1=(this.state.pPos[0]+1)%this.state.number
        var p3=(this.state.pPos[2]-4)%this.state.number
        if(p3<0)p3+=this.state.number
        this.setState({pPos:[p1,2,p3,0]})
       
            if(this.test)
            {
            this.setState({data: [{ownerColor:"red"}]});
            this.test=0;
            }
            else
            {
            this.setState({data: [{ownerColor:"blue"}]});
            this.test=1;
            }
            */
            
        
    }
    render()
    {
const elems=[];
const players=[];
for(let i=0;i<this.state.number;i++)
{
elems.push(<Property key={"Prop"+i} max={this.state.number} clicked={this.state.clicked===i? 1:0} num={i} data={this.state.data[i]} callback={this.callback}></Property>)
}
var r = 15;
for (var i = 0; i < 4; i++) {
    let level=0
    let pos=this.state.pPos[i]
    for(var j=0;j<i;j++)
    {
        if(pos===this.state.pPos[j])level++
    }
    
    players.push(<Player cx={this.state.cx2} cy={this.state.cy2} color={this.colors[i]} r={r}  key={"Cir"+i} level={level} pos={pos} max={this.state.number}></Player>)
}

return (
<div className={css.svgArrow}>
<svg  id="canvas" height={this.state.h} width={this.state.w} viewBox="590 0 820 820" preserveAspectRatio="xMaxYMid meet">
<rect id="background" className={css.background} />
{elems}
<circle fill={this.state.background} cx={this.state.cx2} cy={this.state.cy2} r={this.state.circle.r} className={css.fillInCircle}></circle>
{players}
</svg>
</div>
)
}
}
export default Circle;