import React from 'react';
import Btn from 'react-bootstrap/Button';
function create(p, c, v) {
    var obj = {
        player: p,
        command: c,
        value: v
    };
    return obj;
}

class Button extends React.Component
{
constructor(props)
{
    super(props)
    this.state={
        disabled:props.disabled,
        variant:props.variant,
        text:props.text,
        command:props.command,
        size:props.size||"lg",
        block:(props.size==="sm")?true:false,
        value:props.value
    }
    this.ws=props.ws
    this.handleClick=this.handleClick.bind(this)
}
static getDerivedStateFromProps(props, state) {
    
    return {
        ...props.disabled,
      };
} 
command=JSON.stringify({player:1,command:"refresh",value:1})
sendRequest()
{
    return new Promise((resolve)=>setTimeout(resolve,2000));
}
handleClick()
    {
        var msg=create(1,this.state.command,this.props.value)
        if(this.ws)
        {
            this.ws.send(msg)
        }
        else
        {
            console.log(msg)
        }
        this.setState({disabled:true})
    }
componentDidUpdate()
{
    if(this.state.disabled)
    {
        this.sendRequest().then(()=>this.setState({disabled:false}))
    }
}
render()
{
    return(<Btn 
        variant={this.state.variant}
        disabled={this.state.disabled}
        type="button" 
        size={this.state.size}
        onClick={!this.state.disabled ? this.handleClick: null}
        block={true}>
            {this.state.text}
        </Btn>)
}
}
export default Button;