import React from 'react';
import Circle from './Components/Circle.js'
import css from './App.module.scss';
import Controls from './Components/Controls.js'
import 'bootstrap/dist/css/bootstrap.min.css';
const domain="localhost";
const port="8000"
class App extends React.Component {
  constructor(props)
    {
        super(props);
        this.state={
          gameData: [{type: "property",level:5},{ownerColor: "pink"},{groupColor: "red"}],
          h:window.innerHeight,
          w:window.innerWidth,
          active:false,
          clicked:0
        }
        this.updateDimensions = this.updateDimensions.bind(this);
        this.callback=this.callback.bind(this)
    }
  ws=new WebSocket(`ws:\\\\${domain}:${port}`);
  updateDimensions()
  {
    this.setState({h:window.innerHeight,w:window.innerWidth});
  }

  componentDidMount()
  {
    window.addEventListener("resize", this.updateDimensions);
    this.ws.onopen = () => {
      // on connecting, do nothing but log it to the console
      console.log('Connected to websocket')
      this.setState({active:true})
      }
    this.ws.onmessage = evt => {
      try
      {
          const message = JSON.parse(evt.data);
          this.onMessage(message);
      }
      catch(error)
      {
          console.log(evt.data);
      }
      }
  
    this.ws.onclose = () => {
      this.setState({active:false})
      console.log('disconnected')
      // automatically try to reconnect on connection loss
    }
    this.ws.onerror = err => {
      console.error(
          "Socket encountered error: ",
          err.message,
          "Closing socket"
      )
    }
  }
  onMessage(message)
    {
        console.log(message);
    }
  callback(clicked)
  {
    this.setState({clicked:clicked})
  }
  render()
  {
  return (
      <div className={css.App}>
      <Circle data={this.state.gameData} h={this.state.h} w={this.state.w} callback={this.callback}></Circle>
      <Controls ws={this.state.active? this.ws : null} clicked={this.state.clicked}></Controls>
      </div>
  )
  }
}

export default App;
