import Card from 'react-bootstrap/Card'
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from './Button.js'
import css from './CardE.module.scss'
class CardE extends React.Component
{
constructor(props)
{
    super(props)
    this.state={
        name:props.name||"NAZWA",
        fee:props.fee||100,
        cost:props.cost||200,
        status:props.status||2,
        level:props.level||4,
        moved:props.moved,
        clicked:props.clicked
    }
}
static getDerivedStateFromProps(props, state) {
    
    return {
        ...props,
      };
} 
render()
{
    var elems=[]
    if(this.state.status===1)
    {
        elems.push(<Button key="buy" text="BUY" command="buy" value={this.state.clicked} variant="success" size="sm"></Button>)
    }
    else if (this.state.status===2)
    {
        elems.push(<Button key="sell" text="SELL" command="sell" variant="danger" value={this.state.clicked} size="sm"></Button>)
        if(this.state.level>0)elems.push(<Button key="downgrade" text="DOWNGRADE" command="downgrade" value={this.state.clicked} variant="warning" size="sm"></Button>)
        if(this.state.level<5)elems.push(<Button key="upgrade" text="UPGRADE" command="upgrade" value={this.state.clicked} variant="success" size="sm"></Button>)
    }
    var text=`Current fee: ${this.state.fee}$\n`
    if(this.state.status===1)text+=`Buy cost: ${this.state.cost}$`
    if(this.state.status===2)text+=`Upgrade cost: ${this.state.cost}$`
    return(
    <div className={this.state.moved?css.cardEMoved : css.cardE}>
        <div className={css.Buttons}>
    <Card bg='light' style={{ width: '12rem' }} >
        <Card.Body>
        <Card.Title>{this.state.clicked}<br/>{this.state.name}</Card.Title>
        <Card.Text>
        {text}
      </Card.Text>
        {elems}
        </Card.Body>
    </Card>
    </div>
    </div>
    )
}
}
export default CardE;