
import React from 'react';
import css from './Property.module.scss';
function polarToCartesian(centerX, centerY, radius, angleInDegrees) {
    var angleInRadians = (angleInDegrees - 90) * Math.PI / 180.0;

    return {
        x: centerX + (radius * Math.cos(angleInRadians)),
        y: centerY + (radius * Math.sin(angleInRadians))
    };
}

function describeArc(x, y, radius, startAngle, endAngle) {

    var start = polarToCartesian(x, y, radius, endAngle);
    var end = polarToCartesian(x, y, radius, startAngle);

    var arcSweep = endAngle - startAngle <= 180 ? "0" : "1";

    var d = [
        "M", start.x, start.y,
        "A", radius, radius, 0, arcSweep, 0, end.x, end.y,
        "L", x, y,
        "L", start.x, start.y
    ].join(" ");

    return d;
}
class Property extends React.Component
{
    constructor(props)
    {
        super(props);
        this.test=0;
        this.state={
            background: 'gray',
            cx: 1000,
            cy: 410,
            num: props.num,
            max: props.max,
            sq1: {},
            sq2: {},
            sq3: {},
            sq4: {},
            sq5: {},
            field: {},
            label:{},
            level: 4,
            ownerColor:'#fff4b8',
            groupColor: 'orange',
            fontColor: 'black',
            text:{},
            data:props.data,
            type: "property",
            clickArea:{},
            callback:props.callback,
            clicked:props.clicked
        };
        
    }
    drawSquare(x, y, h, w, c = "green", transform = 0, field, id) {
        var el = {};
        el.id= "house_" + field + "_" + id;
        el.x=x;
        el.y=y;
        el.height=h;
        el.width=w;
        el.fill=c;
        el.transform="rotate(" + transform + "," + this.state.cx + "," + this.state.cy + ")";
        return el;
    }
    drawPath(r,name,color)
    {
        var el={};
        el.id=name + this.state.num;
        el.stroke="#446688";
        el.width= 20;
        el.d=describeArc(this.state.cx, this.state.cy, r, (360 / this.state.max) * (this.state.num - 1 / 2), (360 / this.state.max) * (this.state.num + 1 / 2));
        return el;
    }
    drawText(x, y, txt, transform = 0) {
        var el = {};
        el.id="txt" + this.state.num;
        el.x=x;
        el.y=y;
        el.txt = txt;
        el.transform= "rotate(" + transform + "," + this.state.cx + "," + this.state.cy + ")";
        return el;
    }
    static getDerivedStateFromProps(props, state) {
        if(props.clicked===1)console.log("AA")
        return {
            ...props
          };
    }
    
    componentDidMount()
    {
        //document.getElementById("house_1_1").innerHTML = document.getElementById("house_1_1").innerHTML;
        var sq1=this.drawSquare(980+2.5,40+10,15,15,"blue",360/this.state.max*this.state.num,this.state.num,1);
        var sq2=this.drawSquare(1000+2.5, 40+10, 15, 15,"blue",360/this.state.max*this.state.num,this.state.num,2);
        var sq3=this.drawSquare(980+2.5, 70, 15, 15,"blue",360/this.state.max*this.state.num,this.state.num,3);
        var sq4=this.drawSquare(1000+2.5, 70, 15, 15,"blue",360/this.state.max*this.state.num,this.state.num,4);
        var sq5=this.drawSquare(985, 50, 30, 30,"red",360/this.state.max*this.state.num,this.state.num,5);
        var field,label,clickArea;
        if(this.state.type==="property")
        {
        field=this.drawPath(400,"field");
        label=this.drawPath(410,"label");
        }
        else
        {
        field=this.drawPath(410,"field");
        }
        clickArea=this.drawPath(410,"area");
        var text=this.drawText(1000, 23, "Waszyngton", 360 / this.state.max * this.state.num);
        this.setState({sq1,sq2,sq3,sq4,sq5,field,label,text,clickArea});
        
        if(this.state.data)
        {
        this.setState({ ...this.state.data });
        }
    }
    render()
    {
var core=[];
if(this.state.type==="property")
{
core.push(<path key={"lab"+this.state.label.id} id={this.state.label.id} fill={this.state.groupColor} stroke={this.state.label.stroke} width={this.state.label.width} d={this.state.label.d}></path>)
var houses=[];
if(this.state.level===5)houses.push(<rect key={this.state.sq5.id+"5"} id={this.state.sq5.id} x={this.state.sq5.x} y={this.state.sq5.y} height={this.state.sq5.height} width={this.state.sq5.width} fill={this.state.sq5.fill} transform={this.state.sq5.transform} className={css.Big}></rect>)
else{
if(this.state.level>=1)houses.push(<rect key={this.state.sq1.id+"1"} id={this.state.sq2.id} x={this.state.sq2.x} y={this.state.sq2.y} height={this.state.sq2.height} width={this.state.sq2.width} fill={this.state.sq2.fill} transform={this.state.sq2.transform} className={css.Small}></rect>)
if(this.state.level>=2)houses.push(<rect key={this.state.sq2.id+"2"} id={this.state.sq1.id} x={this.state.sq1.x} y={this.state.sq1.y} height={this.state.sq1.height} width={this.state.sq1.width} fill={this.state.sq1.fill} transform={this.state.sq1.transform} className={css.Small}></rect>)
if(this.state.level>=3)houses.push(<rect key={this.state.sq3.id+"3"} id={this.state.sq3.id} x={this.state.sq3.x} y={this.state.sq3.y} height={this.state.sq3.height} width={this.state.sq3.width} fill={this.state.sq3.fill} transform={this.state.sq3.transform} className={css.Small}></rect>)
if(this.state.level>=4)houses.push(<rect key={this.state.sq4.id+"4"} id={this.state.sq4.id} x={this.state.sq4.x} y={this.state.sq4.y} height={this.state.sq4.height} width={this.state.sq4.width} fill={this.state.sq4.fill} transform={this.state.sq4.transform} className={css.Small}></rect>)
}
}
core.push(<path key={"field"+this.state.label.id}id={this.state.field.id} fill={this.state.ownerColor} stroke={this.state.field.stroke} width={this.state.field.width} d={this.state.field.d}></path>)
core.push(<text key={"txt"+this.state.label.id}id={this.state.text.id} x={this.state.text.x} y={this.state.text.y} className={css.propertyText}  transform={this.state.text.transform}>{this.state.text.txt}</text>)
var area=<path key={"area"+this.state.clickArea.id} className={this.state.clicked?css.AreaClicked:css.Area} id={this.state.clickArea.id} onClick={()=>this.state.callback(this.state.num)}  d={this.state.clickArea.d}></path>

return (
    <g>
{core}
{houses}
{area}
</g>
)
}
}
export default Property;